import React, { useState } from "react";
import { Container, Grid, TextField, Button, Slider, Box, Typography } from "@material-ui/core";

export default function App() {
  const [a, setA] = useState("");
  const [b, setB] = useState("");
  const [c, setC] = useState("");
  const [sliderValueX, setSliderValueX] = useState(2.5);
  const [sliderValueY, setSliderValueY] = useState(1.5);
  const [depositSize, setDepositSize] = useState("");
  const [depositAmount, setDepositAmount] = useState("");
  const [marketSize, setMarketSize] = useState("");
  const [page, setPage] = useState(1);
  const [result, setResult] = useState("");
  const [marketSizeResult, setMarketSizeResult] = useState("");
  const [growthTargetResult, setGrowthTargetResult] = useState("");
  const [showFields, setShowFields] = useState(true);
  const [showAllFields, setShowAllFields] = useState(true);


  const handleCalculate = () => {
    let aVal = parseFloat(a);
    let bVal = parseFloat(b);
    let cVal = parseFloat(c);
    let result;
    setShowFields(false);

    if (a === "" && b !== "" && c !== "") {
      aVal = (bVal / cVal) * 100;
      result = "Deposit Size = " + aVal.toFixed(2) + "%";
      setDepositSize(aVal);
      setDepositAmount(bVal);
      setMarketSize(cVal);
    } else if (a !== "" && b === "" && c !== "") {
      bVal = (aVal / 100) * cVal;
      result = "Deposit = " + bVal.toFixed(2);
      setDepositAmount(bVal);
      setDepositSize(aVal);
      setMarketSize(cVal);
    } else if (a !== "" && b !== "" && c === "") {
      cVal = bVal / (aVal / 100);
      result = "Total Market Size = " + cVal.toFixed(2);
      setMarketSize(cVal);
      setDepositAmount(bVal);
      setDepositSize(aVal);
    } else {
      result = "Please enter any two values";
    }

    console.log(result); // Assuming you'd want to do something with the result
    setResult(result);

  }

  const handleCalculateSlider = (x, y) => {
    let xVal = sliderValueX;
    let yVal = sliderValueY;


    // Calculate the new total market size. Add 100 to x because the growth is in %
    let newMarketSize = marketSize * (1 + xVal / 100);

    // Calculate the amount needed for the growth
    let amountNeeded = (newMarketSize * ((yVal + depositSize) / 100)) - depositAmount;

    // Display the results
    console.log("Total market size = " + newMarketSize.toFixed(2) + ", Amount needed for growth = " + amountNeeded.toFixed(2));

    setMarketSizeResult(newMarketSize.toFixed(2));
    setGrowthTargetResult(amountNeeded.toFixed(2));
  };

  // You might want to manage the slider values with state as well and use them in handleCalculateSlider function
  return (
    <Grid container>
      <Grid item xs={2}></Grid>
      <Grid item xs={8}>
        {showAllFields ? (
          page === 1 ? (
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <h2>Current Market Size</h2>
              </Grid>
              <Grid item xs={12} id="field">
                {showFields && (
                  <TextField
                    style={{ width: "100%" }}
                    type="number"
                    label="Your Current Market Share (in %)"
                    value={a}
                    onChange={(e) => setA(e.target.value)}
                  />
                )}
              </Grid>
              <Grid item xs={12} id="field">
                {showFields && (
                  <TextField
                    style={{ width: "100%" }}
                    type="number"
                    label="Your Current Business Level (in Cr)"
                    value={b}
                    onChange={(e) => setB(e.target.value)}
                  />
                )}
              </Grid>
              <Grid item xs={12} id="field">
                {showFields && (
                  <TextField
                    style={{ width: "100%" }}
                    type="number"
                    label="Total Market Size (in Cr)"
                    value={c}
                    onChange={(e) => setC(e.target.value)}
                  />
                )}
              </Grid>
              <Grid item xs={12}>
                {showFields && (
                  <Button variant="contained" color="primary" onClick={handleCalculate}>
                    Calculate
                  </Button>
                )}

              </Grid>
              <Grid item xs={12}>
                <h3>{result}</h3>
              </Grid>
            </Grid>
          ) : (
            <Box sx={{ width: 250 }}>
              <Grid container spacing={3} alignItems="center">
                <Grid item xs={12}>
                  <h2>Market Size Prediction</h2>
                </Grid>
                <Grid item xs={12}>
                  <h4>Expected Market Growth (in %)</h4>
                </Grid>
                <Grid item xs={6}>
                  <Slider
                    defaultValue={2.5}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"
                    step={0.1}
                    marks
                    min={0.1}
                    max={20}
                    valueLabelDisplay="on"
                    value={sliderValueX}
                    onChange={(e, val) => setSliderValueX(val)} />
                </Grid>
                <Grid item xs={12}>
                  <h4>Your Targetted Growth (in %)</h4>
                </Grid>
                <Grid item xs={6}>
                  <Slider
                    defaultValue={1.5}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"
                    step={0.1}
                    marks
                    min={0.1}
                    max={20}
                    valueLabelDisplay="on"
                    value={sliderValueY}
                    onChange={(e, val) => setSliderValueY(val)} />
                </Grid>
                <Grid item xs={12}>
                  <h3>Total Market Size: {marketSizeResult}</h3>
                  <h3>Amount Needed for Growth: {growthTargetResult}</h3>
                </Grid>
                <Grid item xs={12}>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleCalculateSlider}
                  >
                    Calculate
                  </Button>
                </Grid>
              </Grid>
            </Box>
          )
        ) : null}
        <Button variant="contained" color="primary" onClick={() => setPage((page % 2) + 1)}>
          {page === 1 ? "Get Required Growth Target" : "Calculate Current Market Size"}
        </Button>
      </Grid>

      <Grid item xs={2}></Grid>
    </Grid>
  );
}






